  .col-sm-9 {
    width: 75%; }
  .col-sm-10 {
    width: 83.33%; }
  .col-sm-11 {
    width: 91.66%; }
  .col-sm-12 {
    width: 100%; } }

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-md-1 {
    width: 8.33%; }
  .col-md-2 {
    width: 16.66%; }
  .col-md-3 {
    width: 24%; }
  .col-md-4 {
    width: 33.33%; }
  .col-md-5 {
    width: 41.66%; }
  .col-md-6 {
    width: 50%; }
  .col-md-7 {
    width: 58.33%; }
  .col-md-8 {
