    width: 16.66%; }
  .col-lg-3 {
    width: 24%; }
  .col-lg-4 {
    width: 33.33%; }
  .col-lg-5 {
    width: 41.66%; }
  .col-lg-6 {
    width: 50%; }
  .col-lg-7 {
    width: 58.33%; }
  .col-lg-8 {
    width: 66.66%; }
  .col-lg-9 {
    width: 75%; }
  .col-lg-10 {
    width: 83.33%; }
  .col-lg-11 {
    width: 91.66%; }
  .col-lg-12 {
    width: 100%; } }

.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1140px; } }
