@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.card {
  border-radius: 5px;
  background-color: #2C2A36;
  margin: 5px;
  padding: 10px;
  -webkit-transition: -webkit-transform 2.5s;
  transition: -webkit-transform 2.5s;
  transition: transform 2.5s;
  transition: transform 2.5s, -webkit-transform 2.5s; }
  .card:hover {
    /* IE 9 */
    -webkit-transform: scale(1.5);
    /* Safari 3-8 */
    transform: scale(1.5);
    cursor: pointer; }
  @media only screen and (max-width: 480px) {
    .card:hover {
      -webkit-transform: none;
              transform: none; } }

header {
  background-color: #2C2A36;
  height: 232px; }

img {
  height: auto;
  width: 100%; }

.container:nth-child(1), .container:nth-child(2) {
  margin-bottom: 10px; }

.container:nth-child(1) .row {
  background-color: #2C2A36;
  border-radius: 5px; }

.center {
  display: flex;
  align-items: center;
  justify-content: center; }
  @media only screen and (max-width: 480px) {
    .center {
      margin-left: 15px; } }

.container-span {
  background-color: #2C2A36;
  padding: 10px;
  border-radius: 5px; }
  .container-span button {
    width: 50px;
    border-style: none;
    background-color: #3d3b47;
    margin-left: 10px;
    outline-color: #c5c5c4; }
    .container-span button:hover {
      cursor: pointer; }

.container:nth-child(3) .row {
  margin-left: -5px;
  margin-right: -5px; }
  .container:nth-child(3) .row span h3 {
    color: #2C2A36; }
  @media only screen and (max-width: 480px) {
    .container:nth-child(3) .row {
      margin-left: 0;
      margin-right: 0; } }

h3 {
  color: #828ECE; }

h1 {
  font-size: 58px; }

h4 {
  font-size: 19px; }

